=======================================\n    INDENTATIONS\n    ======================================== */\n/* ========================================\nMIXINS\n* General Use Mixins to ease up reoccuring styles\n* No Components!\n======================================== */\n/* ========================================\nBUTTON COMPONENT\n* Defines a full-width colorful container\n*\n* @size: small|medium|large|custom(e.g.:5%)\n* @indent: small|medium|large|custom(e.g.:5%)\n* @color: color\n======================================== */\n.button.outline {\n  display: inline-block;\n  user-select: none;\n  width: auto;\n  margin-top: 1em;\n  border-radius: .1em;\n  font-size: 1em;\n  cursor: pointer;\n  line-height: 1;\n  text-transform: uppercase;\n  transition: all 0.5s ease;\n  font-family: 'Merriweather Sans', verdana;\n  background-color: transparent;\n  color: #f4f1f2;\n  border: 1px solid #f4f1f2;\n}\n.button.outline:hover {\n  background-color: #f4f1f2;\n  color: #ff0044;\n}\n.button.reverse {\n  display: inline-block;\n  user-select: none;\n  width: auto;\n  margin-top: 1em;\n  border-radius: .1em;\n  font-size: 1em;\n  cursor: pointer;\n  line-height: 1;\n  text-transform: uppercase;\n  transition: all 0.5s ease;\n  font-family: 'Merriweather Sans', verdana;\n  background-color: transparent;\n  color: #ff0044;\n  border: 1px solid #ff0044;\n}\n.button.reverse:hover {\n  color: #f4f1f2;\n  background-color: #ff0044;\n}\n/* ========================================\nCARD COMPONENT\n* Defines a rectangular area, used for a specific kind of content\n* containing a title, description and actions (buttons/icons)\n* a card is often used together with grid systems to evenly\n* distribute related content.\n* Cards contain little and scannable content to quickly get to the desired\n* full content. Thus, a description should contain not more than 140 letters\n*\n* @type\n======================================== */\n/* ========================================\nSTRIPE COMPONENT\n* Defines a full-width colorful container\n*\n* @padding: small|medium|large|custom(e.g.:5%)\n* @width: small|medium|large|custom(e.g.:300)\n* this value is substracted from the current breakpoint\n* @color: color\n======================================== */\n.stripe.fluid {\n  padding: 0;\n}\n.stripe.fluid > * {\n  width: 100%;\n}\n.stripe.medium {\n  padding: 2em 0;\n  width: 100%;\n  padding-top: 4em;\n  padding-bottom: 4em;\n}\n.stripe.medium > * {\n  margin-left: auto;\n  margin-right: auto;\n}\n@media screen and (max-width: 600px) {\n  .stripe.medium > * {\n    padding-left: 5%;\n    padding-right: 5%;\n  }\n}\n@media screen and (min-width: 600px) {\n  .stripe.medium > * {\n    width: 493.33333333px;\n  }\n}\n@media screen and (min-width: 960px) {\n  .stripe.medium > * {\n    width: 800px;\n  }\n}\n@media screen and (min-width: 1200px) {\n  .stripe.medium > * {\n    width: 880px;\n  }\n}\n.stripe.small {\n  padding: 2em 0;\n  width: 100%;\n  padding-top: 4em;\n  padding-bottom: 4em;\n}\n.stripe.small > * {\n  margin-left: auto;\n  margin-right: auto;\n}\n@media screen and (max-width: 600px) {\n  .stripe.small > * {\n    padding-left: 5%;\n    padding-right: 5%;\n  }\n}\n@media screen and (min-width: 600px) {\n  .stripe.small > * {\n    width: 386.66666667px;\n  }\n}\n@media screen and (min-width: 960px) {\n  .stripe.small > * {\n    width: 640px;\n  }\n}\n@media screen and (min-width: 1200px) {\n  .stripe.small > * {\n    width: 560px;\n  }\n}\n/* ========================================\nBASE\n* Contains HTML default elements (b,i,u,table...)\n* Does not contain classes, ids\n* Can implement mixins\n======================================== */\n* {\n  box-sizing: border-box;\n}\n::-webkit-scrollbar {\n  width: .5em;\n}\n::-webkit-scrollbar-thumb {\n  background: rgba(56, 46, 49, 0.5);\n}\n::-webkit-scrollbar-track {\n  background: rgba(244, 241, 242, 0.5);\n}\n/*  ========================================\n    BLOCK ELEMENTS\n    ============================